.bloque-login {
    background-image: url("https://res.cloudinary.com/dfmgas1qy/image/upload/v1693010072/fondo-sin-logo.jpg.jpg");
    width: 100%;
    min-height: 100vh;
    max-height: 100%;
    justify-content: center;
    align-items: center;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}


.bloque-form-login {
    background-color: white;
    border-radius: 10px;
    height: 400px;
    width: 400px;
    display: flex;
    flex-direction: column;

    align-items: center;
}

.bloque-form-login>img {
    margin-top: 10px;
}

.bloque-form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
}
.element-boton {
    border-radius: 40px;
    margin-top: 30px;
    padding: 10px 30px 10px 30px;
    font-size: 15px;
    color: white;
}
.element-boton.Login {
    background-color: #1477d2;
}


.Footer-BotonBusqueda {
    display: flex;
    align-items: end;
    width: 100%;
    justify-content: center;
    align-items: center;
    margin-bottom: 15px;
    margin-top: 130px;
}

.ContainerConImagen>img {
    width: 90px;
    height: auto;
}

.ContainerConImagen>p {
    color: white;
}

.Footer {
    width: 100%;
    height: 100px;
    display: flex;
    flex-direction: row;
}

.Footer-BotonBusqueda>p {
    color: white;
}

.ContainerConImagen {
    display: flex;
    flex-direction: row;
    align-items: center;
}



@media (max-width: 800px) {
    .Footer-BotonBusqueda {
        flex-direction: column;
    }

    .Separador {
        visibility: hidden;
    }
}