.element-boton {
    border-radius: 40px;
    
    padding: 10px 30px 10px 30px;
    font-size: 15px;
    color: white;
}

.element-boton.cerrar {
    background-color: red;
}

.element-boton.add {
    background-color: green;
}
.element-botones-accion {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}
.element-boton.agregar-cliente{
    background-color: #1477d2;
    width: 17%;
}
.element-boton.agregar-producto-proveedor{
    background-color: #1477d2;
    border-radius: 15px;
}