.bloque-filtro-productos {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}
.element-input {
    padding: 5px;
    display: flex;
    flex-direction: row;
    min-width: 150px;
    max-width: 500px;
    align-items: center;
    justify-content: center;
}
.modifier-buscar {
    margin: 15px;
}

td>img {
    margin-left: 30%;
}

.element-edit-bar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
}

.element-boton.Guardar {
    background-color: #0747a1;
}

.element-save-bar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
}

.element-botones-accion {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.element-boton {
    border-radius: 40px;

    padding: 10px 30px 10px 30px;
    font-size: 15px;
    color: white;
}

.element-boton.cerrar {
    background-color: red;
}

.element-boton.add {
    background-color: green;
}
.element-boton.Holder{
    border: 1px green solid;
    color: green;
}
.element-boton.Holder:hover{
    border: 1px green solid;
    background-color: green;
    color: white;
}

.modifier-button {
    border-radius: 20px;
    padding: 10px 30px 10px 30px;
    font-size: 15px;
    color: white;
    background-color: #1477d2;
    margin: 5px;
    border-radius: 10px;
}
.element-input-Agregar {
    padding: 5px;
    display: flex;
    flex-direction: column;
    min-width: 150px;
    max-width: 500px;
    align-items: center;
    justify-content: center;
}
.bloque-botones-accion{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    margin-top: 30px;
}

.bloque-adv-eliminar{
    padding: 10px;
}